          <td class="tdcon">19</td>
          <td class="bg-write">
            <input name="19_1" readonly value="0.00"/>
          </td>
          <td class="bg-write">
            <input name="19_2" readonly value="0.00"/>
          </td>
        </tr>
        <tr>
          <td><b>四、现金净增加额</b></td>
          <td class="tdcon">20</td>
          <td class="bg-write">
            <input name="20_1" readonly value="0.00"/>
          </td>
          <td class="bg-write">
            <input name="20_2" readonly value="0.00"/>
          </td>
        </tr>
        <tr>
          <td>　　　加：期初现金余额</td>
          <td class="tdcon">21</td>
          <td class="bg-unwrite">
            <input name="21_1" readonly value="0.00"/>
          </td>
