    </form>
    <p><b>说明：按季报送现金流量表的，“本月金额”列填列本季度内各月合计数。</b></p>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 30,  //当前表格的名称编号
        setting: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        //第7行计算
        this.fn2('7_2', [1, 2, 3, 4, 5, 6], 1)
        //第13行计算
        this.fn2('13_2', [8, 9, 10, 11, 12], 2)
        //第19行计算
        this.fn2('19_2', [14, 15, 16, 17, 18], 1)
        //第20行和22行
        this.fn2('20_2', [7, 13, 19], 3)
        this.fn2('22_2', [20, 21], 3)
        //本年累计金额计算
        this.fn1(1, 22, [2])
      },
      //(
      // q  要修改值的列
      // r  要修改多少行
      // seed 每行参与计算的列
      // {
      //    1加 a=a+b
      // }
      // )
      fn1(q, r, seed) {
        for (let i = 1; i <= r; i++) {
          var sum = 0
          for (let j = 0; j < seed.length; j++) {
            let m = seed[j]
            let dom = J('input[name=' + i + '_' + m + ']')
            if (dom.val()) {
              if (!isNaN(dom.val())) {
                if (this.setting[i + '_' + q]) {
                } else {
                  this.setting[i + '_' + q] = 0
                }
                sum += parseFloat(1 * (dom.val()) + 1 * (this.setting[i + '_' + q]))
              }
            }
          }
          J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
        }
      },
      // q  需计算单元格的name
      // gump 参与计算的行
      // type 公式{
      //  1:a=b+c-d-e-f-g
      //  2:a=b+c+d-e-f
      //  3:a=b+c...
      // }
      fn2(q, gump, type) {
        if (J('input[name=' + gump[0] + '_2]').val()) {
          var sum = parseFloat(J('input[name=' + gump[0] + '_2]').val())
        } else {
          var sum = 0
        }
        for (let i = 1; i < gump.length; i++) {
          let dom = J('input[name=' + gump[i] + '_2]')
          if (dom.val()) {
            if (type == 1) {
              if (i == 1) {
                sum += parseFloat(dom.val())
              } else {
                sum -= parseFloat(dom.val())
              }
            } else if (type == 2) {
              if (i == 1 || i == 2) {
                sum += parseFloat(dom.val())
              } else {
                sum -= parseFloat(dom.val())
              }
            } else if (type == 3) {
              sum += parseFloat(dom.val())
            }
          }
        }
        J('input[name=' + q + ']').val(sum.toFixed(2))
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    text-align: left;
    padding: 3px 2px;
  }

  .tdcon {
    text-align: center;
  }

  .p2 {
    position: relative;
  }

  .bg-write {
    background: #ccccff;
  }

  .bg-unwrite {
    background: #d6ebff;
  }
</style>



